:["<template>\r\n    <div class=\"wed-host-section container mx-auto\">\r\n        <div class=\"wed-host-section-container\">\r\n            <div>\r\n                <div class=\"wed-cere-head\">\r\n                    <h1>Wedding ceremonies</h1>\r\n                    <button\r\n                        v-if=\"showForm == false\"\r\n                        @click=\"\r\n                            showForm = true;\r\n                            active = totalCeramonies[0]['name'];\r\n                        \"\r\n                    >\r\n                        <span>+</span>Add Ceremony\r\n                    </button>\r\n                    <button\r\n                        v-if=\"\r\n                            showForm == true &&\r\n                            totalCeramonies.length > 0 &&\r\n                            inviCeramonies != 0\r\n                        \"\r\n                        @click=\"closeForm()\"\r\n                    >\r\n                        Close\r\n                    </button>\r\n                </div>\r\n                <div v-if=\"totalCeramonies.length == 0 || showForm == true\">\r\n                    <div v-if=\"screenWidth > 768\">\r\n                        <div v-if=\"edit == false\" class=\"wed-cere-title-cont\">\r\n                            <div\r\n                                v-for=\"(item, index) in totalCeramonies\"\r\n                                :key=\"index\"\r\n                                :class=\"\r\n                                    item['name'] == active\r\n                                        ? 'wed-cere-title-single title-active'\r\n                                        : 'wed-cere-title-single'\r\n                                \"\r\n                                @click=\"switchForm($event, item)\"\r\n                            >\r\n                                {{ titleCase(item[\"name\"]) }}\r\n                            </div>\r\n                        </div>\r\n\r\n                        <div v-else class=\"wed-cere-title-cont\">\r\n                            <div class=\"wed-cere-title-single title-active\">\r\n                                {{ titleCase(active) }}\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                    <div v-else class=\"mobile-list-cont\">\r\n                        <div\r\n                            @click.stop=\"showDropDown = !showDropDown\"\r\n                            class=\"mobile-list-head\"\r\n                        >\r\n                            {{ titleCase(active) }}\r\n                            <img\r\n                                v-if=\"\r\n                                    edit == false && totalCeramonies.length != 1\r\n                                \"\r\n                                src=\"/assets/ceramonies/icon-down.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                        </div>\r\n                        <div\r\n                            v-if=\"showDropDown && edit == false\"\r\n                            class=\"mobile-list-content-cont\"\r\n                        >\r\n                            <div\r\n                                v-for=\"(item, index) in filteredList\"\r\n                                :key=\"index\"\r\n                                class=\"mobile-list-single\"\r\n                                @click=\"switchForm($event, item)\"\r\n                            >\r\n                                {{ titleCase(item[\"name\"]) }}\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"wed-cere-content\">\r\n                        <h3 class=\"wed-cere-content-title\">\r\n                            {{ titleCase(active) }}\r\n                        </h3>\r\n                        <ValidationObserver\r\n                            v-slot=\"{ handleSubmit, invalid }\"\r\n                            :class=\"{ blur: loader }\"\r\n                            ref=\"formObserver\"\r\n                        >\r\n                            <form\r\n                                @submit.prevent=\"\r\n                                    handleSubmit(\r\n                                        sendCeremony(invalid, $event, active)\r\n                                    )\r\n                                \"\r\n                            >\r\n                                <ValidationProvider\r\n                                    v-if=\"active == 'Custom'\"\r\n                                    rules=\"required|min:3|max:20|alpha_spaces\"\r\n                                    v-slot=\"{ errors }\"\r\n                                >\r\n                                    <div\r\n                                        class=\"form-group-custom\"\r\n                                        :class=\"{\r\n                                            'form-group--error':\r\n                                                errors.length > 0,\r\n                                        }\"\r\n                                    >\r\n                                        <label for=\"title\" class=\"form-label\"\r\n                                            >Display Name</label\r\n                                        >\r\n                                        <input\r\n                                            type=\"text\"\r\n                                            name=\"title\"\r\n                                            id=\"title\"\r\n                                            class=\"form-control\"\r\n                                            placeholder=\"Ceremony Name\"\r\n                                            v-model=\"ceremonyData.name\"\r\n                                        />\r\n                                        <span class=\"errMsg\">{{\r\n                                            errors[0]\r\n                                        }}</span>\r\n\r\n                                        <span\r\n                                            v-if=\"\r\n                                                errorsSubmit &&\r\n                                                errorsSubmit.name\r\n                                            \"\r\n                                            class=\"errMsg\"\r\n                                            >{{ errorsSubmit.name[0] }}</span\r\n                                        >\r\n                                    </div>\r\n                                </ValidationProvider>\r\n                                <ValidationProvider\r\n                                    rules=\"image|mimes:image/jpeg,image/png\"\r\n                                    name=\"Ceremony Image\"\r\n                                    ref=\"ceramonyRef\"\r\n                                    v-slot=\"{ errors }\"\r\n                                >\r\n                                    <div class=\"form-group-custom\">\r\n                                        <label for=\"title\" class=\"form-label\"\r\n                                            >Display Image</label\r\n                                        >\r\n                                        <div\r\n                                            class=\"upload-btn-cont disp-image-feild\"\r\n                                        >\r\n                                            <span>{{ imageOneMessage }}</span>\r\n                                            <input\r\n                                                @change=\"\r\n                                                    uploadImage(\r\n                                                        $event,\r\n                                                        'cereImage'\r\n                                                    )\r\n                                                \"\r\n                                                ref=\"dispImage\"\r\n                                                type=\"file\"\r\n                                                name=\"CeremonyImage\"\r\n                                            />\r\n                                            <button\r\n                                                @click=\"$refs.dispImage.click()\"\r\n                                                type=\"button\"\r\n                                            >\r\n                                                <img\r\n                                                    src=\"/assets/miscellenous/upload.svg\"\r\n                                                    alt=\"\"\r\n                                                />\r\n                                                Upload\r\n                                            </button>\r\n                                        </div>\r\n                                        <span class=\"errMsg\">{{\r\n                                            errors[0]\r\n                                        }}</span>\r\n\r\n                                        <span\r\n                                            v-if=\"\r\n                                                errorsSubmit &&\r\n                                                errorsSubmit.imageOne\r\n                                            \"\r\n                                            class=\"errMsg\"\r\n                                            >{{\r\n                                                errorsSubmit.imageOne[0]\r\n                                            }}</span\r\n                                        >\r\n                                    </div>\r\n                                </ValidationProvider>\r\n\r\n                                <div class=\"date-grp\">\r\n                                    <ValidationProvider\r\n                                        rules=\"required\"\r\n                                        v-slot=\"{ errors }\"\r\n                                    >\r\n                                        <div\r\n                                            class=\"form-group-custom\"\r\n                                            :class=\"{\r\n                                                'form-group--error':\r\n                                                    errors.length > 0,\r\n                                            }\"\r\n                                        >\r\n                                            <input\r\n                                                :type=\"\r\n                                                    ceremonyData?.startDate\r\n                                                        ? 'date'\r\n                                                        : 'text'\r\n                                                \"\r\n                                                name=\"completionDate\"\r\n                                                onfocus=\"(this.type='date')\"\r\n                                                class=\"form-date form-control\"\r\n                                                placeholder=\"Start date\"\r\n                                                v-model=\"ceremonyData.startDate\"\r\n                                                ref=\"typedate\"\r\n                                            />\r\n                                            <span class=\"errMsg\">{{\r\n                                                errors[0]\r\n                                            }}</span>\r\n\r\n                                            <span\r\n                                                v-if=\"\r\n                                                    errorsSubmit &&\r\n                                                    errorsSubmit.startDate\r\n                                                \"\r\n                                                class=\"errMsg\"\r\n                                                >{{\r\n                                                    errorsSubmit.startDate[0]\r\n                                                }}</span\r\n                                            >\r\n                                        </div>\r\n                                    </ValidationProvider>\r\n                                    <ValidationProvider\r\n                                        rules=\"required\"\r\n                                        v-slot=\"{ errors }\"\r\n                                    >\r\n                                        <div\r\n                                            class=\"form-group-custom\"\r\n                                            :class=\"{\r\n                                                'form-group--error':\r\n                                                    errors.length > 0,\r\n                                            }\"\r\n                                        >\r\n                                            <input\r\n                                                :type=\"\r\n                                                    ceremonyData?.startDate\r\n                                                        ? 'time'\r\n                                                        : 'text'\r\n                                                \"\r\n                                                name=\"completionDate\"\r\n                                                onfocus=\"(this.type='time')\"\r\n                                                class=\"form-date form-control\"\r\n                                                placeholder=\"Start time\"\r\n                                                v-model=\"ceremonyData.startTime\"\r\n                                                ref=\"typetime\"\r\n                                            />\r\n                                            <span class=\"errMsg\">{{\r\n                                                errors[0]\r\n                                            }}</span>\r\n\r\n                                            <span\r\n                                                v-if=\"\r\n                                                    errorsSubmit &&\r\n                                                    errorsSubmit.startTime\r\n                                                \"\r\n                                                class=\"errMsg\"\r\n                                                >{{\r\n                                                    errorsSubmit.startTime[0]\r\n                                                }}</span\r\n                                            >\r\n                                        </div>\r\n                                    </ValidationProvider>\r\n                                </div>\r\n                                <div>\r\n                                    <div class=\"dress-code-toggle-cont\">\r\n                                        <span>Dress Code</span>\r\n                                        <label class=\"switch\" for=\"toggle-btn\">\r\n                                            <!-- <input\r\n                                                v-model=\"toggle\"\r\n                                                type=\"checkbox\"\r\n                                                id=\"toggle-btn\"\r\n\r\n                                            /> -->\r\n                                            <span\r\n                                                @click=\"toggle = !toggle\"\r\n                                                class=\"slider round\"\r\n                                                :class=\"{ checked: toggle }\"\r\n                                            ></span>\r\n                                        </label>\r\n                                    </div>\r\n                                    <div v-if=\"toggle\" class=\"dress-code-cont\">\r\n                                        <div>\r\n                                            <ValidationProvider\r\n                                                rules=\"min:3|max:20|alpha_spaces\"\r\n                                                v-slot=\"{ errors }\"\r\n                                            >\r\n                                                <div\r\n                                                    class=\"form-group-custom\"\r\n                                                    :class=\"{\r\n                                                        'form-group--error':\r\n                                                            errors.length > 0,\r\n                                                    }\"\r\n                                                >\r\n                                                    <label\r\n                                                        for=\"ladiesDress\"\r\n                                                        class=\"form-label\"\r\n                                                    ></label>\r\n                                                    <input\r\n                                                        type=\"text\"\r\n                                                        id=\"ladiesDress\"\r\n                                                        class=\"form-control\"\r\n                                                        placeholder=\"Me dress code Eg: Black suit\"\r\n                                                        v-model=\"\r\n                                                            ceremonyData.ladiesDress\r\n                                                        \"\r\n                                                    />\r\n                                                    <span class=\"errMsg\">{{\r\n                                                        errors[0]\r\n                                                    }}</span>\r\n\r\n                                                    <span\r\n                                                        v-if=\"\r\n                                                            errorsSubmit &&\r\n                                                            errorsSubmit.ladiesDress\r\n                                                        \"\r\n                                                        class=\"errMsg\"\r\n                                                        >{{\r\n                                                            errorsSubmit\r\n                                                                .ladiesDress[0]\r\n                                                        }}</span\r\n                                                    >\r\n                                                </div>\r\n                                            </ValidationProvider>\r\n                                            <ValidationProvider\r\n                                                rules=\"image|mimes:image/jpeg,image/png\"\r\n                                                name=\"Ladies Dress\"\r\n                                                ref=\"ladiesRef\"\r\n                                                v-slot=\"{ errors }\"\r\n                                            >\r\n                                                <div class=\"upload-btn-cont\">\r\n                                                    <input\r\n                                                        @change=\"\r\n                                                            uploadImage(\r\n                                                                $event,\r\n                                                                'ladiesDress'\r\n                                                            )\r\n                                                        \"\r\n                                                        ref=\"ladyDress\"\r\n                                                        type=\"file\"\r\n                                                    />\r\n                                                    <button\r\n                                                        @click=\"\r\n                                                            $refs.ladyDress.click()\r\n                                                        \"\r\n                                                        type=\"button\"\r\n                                                    >\r\n                                                        <img\r\n                                                            src=\"/assets/miscellenous/upload.svg\"\r\n                                                            alt=\"\"\r\n                                                        />\r\n                                                        Upload\r\n                                                    </button>\r\n                                                    <span>{{\r\n                                                        imageTwoMessage\r\n                                                    }}</span>\r\n                                                </div>\r\n                                                <span class=\"errMsg\">{{\r\n                                                    errors[0]\r\n                                                }}</span>\r\n\r\n                                                <span\r\n                                                    v-if=\"\r\n                                                        errorsSubmit &&\r\n                                                        errorsSubmit.imageTwo\r\n                                                    \"\r\n                                                    class=\"errMsg\"\r\n                                                    >{{\r\n                                                        errorsSubmit.imageTwo[0]\r\n                                                    }}</span\r\n                                                >\r\n                                            </ValidationProvider>\r\n                                        </div>\r\n                                        <div>\r\n                                            <ValidationProvider\r\n                                                rules=\"min:3|max:20|alpha_spaces\"\r\n                                                v-slot=\"{ errors }\"\r\n                                            >\r\n                                                <div\r\n                                                    class=\"form-group-custom\"\r\n                                                    :class=\"{\r\n                                                        'form-group--error':\r\n                                                            errors.length > 0,\r\n                                                    }\"\r\n                                                >\r\n                                                    <label\r\n                                                        for=\"menDress\"\r\n                                                        class=\"form-label\"\r\n                                                    ></label>\r\n                                                    <input\r\n                                                        type=\"text\"\r\n                                                        name=\"menDress\"\r\n                                                        id=\"menDress\"\r\n                                                        class=\"form-control\"\r\n                                                        placeholder=\"Ladies dress code Eg: Black Lehenga\"\r\n                                                        v-model=\"\r\n                                                            ceremonyData.menDress\r\n                                                        \"\r\n                                                    />\r\n                                                    <span class=\"errMsg\">{{\r\n                                                        errors[0]\r\n                                                    }}</span>\r\n\r\n                                                    <span\r\n                                                        v-if=\"\r\n                                                            errorsSubmit &&\r\n                                                            errorsSubmit.menDress\r\n                                                        \"\r\n                                                        class=\"errMsg\"\r\n                                                        >{{\r\n                                                            errorsSubmit\r\n                                                                .menDress[0]\r\n                                                        }}</span\r\n                                                    >\r\n                                                </div>\r\n                                            </ValidationProvider>\r\n                                            <ValidationProvider\r\n                                                rules=\"image|mimes:image/jpeg,image/png\"\r\n                                                name=\"Mens Dress\"\r\n                                                ref=\"gentsRef\"\r\n                                                v-slot=\"{ errors }\"\r\n                                            >\r\n                                                <div class=\"upload-btn-cont\">\r\n                                                    <input\r\n                                                        @change=\"\r\n                                                            uploadImage(\r\n                                                                $event,\r\n                                                                'gentsDress'\r\n                                                            )\r\n                                                        \"\r\n                                                        ref=\"menDress\"\r\n                                                        type=\"file\"\r\n                                                    />\r\n                                                    <button\r\n                                                        @click=\"\r\n                                                            $refs.menDress.click()\r\n                                                        \"\r\n                                                        type=\"button\"\r\n                                                    >\r\n                                                        <img\r\n                                                            src=\"/assets/miscellenous/upload.svg\"\r\n                                                            alt=\"\"\r\n                                                        />\r\n                                                        Upload\r\n                                                    </button>\r\n                                                    <span>{{\r\n                                                        imageThreeMessage\r\n                                                    }}</span>\r\n                                                </div>\r\n                                                <span class=\"errMsg\">{{\r\n                                                    errors[0]\r\n                                                }}</span>\r\n\r\n                                                <span\r\n                                                    v-if=\"\r\n                                                        errorsSubmit &&\r\n                                                        errorsSubmit.imageThree\r\n                                                    \"\r\n                                                    class=\"errMsg\"\r\n                                                    >{{\r\n                                                        errorsSubmit\r\n                                                            .imageThree[0]\r\n                                                    }}</span\r\n                                                >\r\n                                            </ValidationProvider>\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                                <ValidationProvider\r\n                                    rules=\"required\"\r\n                                    v-slot=\"{ errors }\"\r\n                                >\r\n                                    <div\r\n                                        class=\"form-group-custom\"\r\n                                        :class=\"{\r\n                                            'form-group--error':\r\n                                                errors.length > 0,\r\n                                        }\"\r\n                                        style=\"margin-top: 7px\"\r\n                                    >\r\n                                        <label class=\"form-label\">Venues</label>\r\n                                        <select\r\n                                            name=\"category_id\"\r\n                                            class=\"form-control\"\r\n                                            v-model=\"venueid\"\r\n                                        >\r\n                                            <option\r\n                                                v-for=\"(venue, id) in venues\"\r\n                                                :key=\"venue.id\"\r\n                                                :value=\"venue.id\"\r\n                                            >\r\n                                                {{ venue.name }}\r\n                                            </option>\r\n                                        </select>\r\n                                        <span class=\"errMsg\">{{\r\n                                            errors[0]\r\n                                        }}</span>\r\n\r\n                                        <span\r\n                                            v-if=\"\r\n                                                errorsSubmit &&\r\n                                                errorsSubmit.venueid\r\n                                            \"\r\n                                            class=\"errMsg\"\r\n                                            >{{ errorsSubmit.venueid[0] }}</span\r\n                                        >\r\n                                    </div>\r\n                                </ValidationProvider>\r\n                                <div class=\"add-venue-link-cont\">\r\n                                    <a href=\"/host/venues/create\"\r\n                                        >Could’nt find a perfect venue? Add\r\n                                        One!!</a\r\n                                    >\r\n                                </div>\r\n                                <div class=\"form-group-custom ml-5\">\r\n                                    <button\r\n                                        type=\"submit\"\r\n                                        :disabled=\"disableSubmit\"\r\n                                    >\r\n                                        Submit\r\n                                    </button>\r\n                                </div>\r\n                            </form>\r\n                        </ValidationObserver>\r\n                    </div>\r\n                </div>\r\n                <div v-else class=\"cere-view-cont\">\r\n                    <div\r\n                        v-for=\"(item, index) in inviCeramonies\"\r\n                        :key=\"item.id\"\r\n                        class=\"cere-view-single\"\r\n                    >\r\n                        <div class=\"cere-single-image-cont\">\r\n                            <img :src=\"'/storage/' + item.imageOne\" alt=\"\" />\r\n                            <div>\r\n                                <img\r\n                                    :class=\"{\r\n                                        disabled: disabledEdit,\r\n                                    }\"\r\n                                    @click=\"editCeramony(index, item)\"\r\n                                    src=\"/assets/ceramonies/edit.svg\"\r\n                                    alt=\"\"\r\n                                />\r\n                                <img\r\n                                    src=\"/assets/ceramonies/trash.svg\"\r\n                                    alt=\"\"\r\n                                    :class=\"{\r\n                                        disabled: disabledDelete,\r\n                                    }\"\r\n                                    @click=\"deleteCeramony(index, item)\"\r\n                                />\r\n                            </div>\r\n                        </div>\r\n                        <div class=\"cere-view-info\">\r\n                            <h3>{{ titleCase(item.name) }}</h3>\r\n                            <div class=\"cere-view-info-time\">\r\n                                <div>\r\n                                    <img\r\n                                        src=\"/assets/ceramonies/calendar.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                </div>\r\n                                <div>\r\n                                    <div>\r\n                                        {{\r\n                                            new Date(\r\n                                                item.startDate\r\n                                            ).toLocaleDateString(\"en-us\", {\r\n                                                weekday: \"long\",\r\n                                                year: \"numeric\",\r\n                                                month: \"long\",\r\n                                                day: \"numeric\",\r\n                                            })\r\n                                        }}\r\n                                    </div>\r\n                                    <div>\r\n                                        {{\r\n                                            new Date(\r\n                                                \"1970-01-01T\" +\r\n                                                    item.startTime +\r\n                                                    \"Z\"\r\n                                            ).toLocaleTimeString(\"en-US\", {\r\n                                                timeZone: \"UTC\",\r\n                                                hour12: true,\r\n                                                hour: \"numeric\",\r\n                                                minute: \"numeric\",\r\n                                            })\r\n                                        }}\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                            <div class=\"cere-view-info-time\">\r\n                                <div>\r\n                                    <img\r\n                                        src=\"/assets/ceramonies/map-pin.svg\"\r\n                                        alt=\"\"\r\n                                    />\r\n                                </div>\r\n                                <div>\r\n                                    <div v-for=\"e in item.venues\" :key=\"e.name\">\r\n                                        {{ e.name }}, {{ e.location.name }},\r\n                                        {{ e.location.state }}\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                            <div\r\n                                v-if=\"\r\n                                    item.menDress ||\r\n                                    item.ladiesDress ||\r\n                                    item.imageTwo ||\r\n                                    item.imageThree\r\n                                \"\r\n                                class=\"cere-view-dress-cont\"\r\n                            >\r\n                                <h3>Dress Code</h3>\r\n                                <div v-if=\"item.menDress || item.imageTwo\">\r\n                                    <span>\r\n                                        <img\r\n                                            src=\"/assets/ceramonies/carbon_gender-male.svg\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                        <span>Men:</span>\r\n                                    </span>\r\n                                    <span v-if=\"item.menDress\">\r\n                                        {{ item.menDress }}\r\n                                    </span>\r\n                                    <div v-if=\"item.imageTwo\">\r\n                                        <img\r\n                                            class=\"dress-img\"\r\n                                            :src=\"'/storage/' + item.imageTwo\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                    </div>\r\n                                </div>\r\n                                <div v-if=\"item.ladiesDress || item.imageThree\">\r\n                                    <span>\r\n                                        <img\r\n                                            src=\"/assets/ceramonies/carbon_gender-female.svg\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                        <span>Women:</span>\r\n                                    </span>\r\n                                    <span v-if=\"item.ladiesDress\">\r\n                                        {{ item.ladiesDress }}\r\n                                    </span>\r\n                                    <div v-if=\"item.imageThree\">\r\n                                        <img\r\n                                            class=\"dress-img\"\r\n                                            :src=\"'/storage/' + item.imageThree\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <flashMessage :message=\"message\"></flashMessage>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport flashMessage from \"../../../FlashMessage.vue\";\r\n\r\nexport default {\r\n    components: {\r\n        flashMessage,\r\n    },\r\n    props: [\"host\", \"invitation\", \"ceramonies\", \"venues\", \"ceramonylist\"],\r\n    data() {\r\n        return {\r\n            venueid: null,\r\n\r\n            active: this.ceramonylist[0][\"name\"] ?? null,\r\n            toggle: false,\r\n            ceremonyData: {\r\n                name: this.ceramonylist[0][\"name\"] ?? null,\r\n                // ceremony_id: this.ceramonylist[0][\"id\"] ?? null,\r\n            },\r\n            inviCeramonies: this.ceramonies,\r\n            totalCeramonies: this.ceramonylist,\r\n            imageOne: null,\r\n            imageTwo: null,\r\n            imageThree: null,\r\n            imageOneMessage: \"Upload Image\",\r\n            imageTwoMessage: null,\r\n            imageThreeMessage: null,\r\n            showForm: false,\r\n            disableSubmit: false,\r\n            disabledDelete: false,\r\n            disabledEdit: false,\r\n            edit: false,\r\n            editCera: null,\r\n            message: null,\r\n            showDropDown: false,\r\n            screenWidth: window.screen.width,\r\n            errorsSubmit: null,\r\n            loader: false,\r\n        };\r\n    },\r\n    methods: {\r\n        switchForm(e, id) {\r\n            let _this = this;\r\n\r\n            //let check = this.ceramonies.find(x => x.name == id);\r\n            if (_this.edit == true) {\r\n                return;\r\n            }\r\n            if (id != this.active) {\r\n                this.active = id[\"name\"];\r\n\r\n                this.setFeilds();\r\n            } else {\r\n                this.active = id;\r\n            }\r\n        },\r\n        closeForm() {\r\n            this.setFeilds();\r\n            this.edit = false;\r\n            this.showDropDown = true;\r\n            this.showForm = false;\r\n        },\r\n        titleCase(str) {\r\n            let _this = this;\r\n            var splitStr = str.toLowerCase().split(\" \");\r\n            for (var i = 0; i < splitStr.length; i++) {\r\n                // You do not need to check if i is larger than splitStr length, as your for does that for you\r\n                // Assign it back to the array\r\n                splitStr[i] =\r\n                    splitStr[i].charAt(0).toUpperCase() +\r\n                    splitStr[i].substring(1);\r\n            }\r\n            // Directly return the joined string\r\n            return splitStr.join(\" \");\r\n        },\r\n        setFeilds() {\r\n            let _this = this;\r\n            this.$refs.formObserver.reset();\r\n            _this.toggle = false;\r\n            _this.venueid = null;\r\n            _this.$refs.typedate.type = \"text\";\r\n            _this.$refs.typetime.type = \"text\";\r\n            _this.imageOne = null;\r\n            _this.imageOneMessage = \"Upload Image\";\r\n            _this.imageTwo = null;\r\n            _this.imageTwoMessage = null;\r\n            _this.imageThree = null;\r\n            _this.imageThreeMessage = null;\r\n            _this.ceremonyData = {\r\n                name: this.active,\r\n            };\r\n        },\r\n        async uploadImage(e, type) {\r\n            let _this = this;\r\n            if (type == \"cereImage\") {\r\n                await this.$refs.ceramonyRef.validate(e);\r\n                this.imageOne = e.target.files[0];\r\n                this.imageOneMessage = e.target.files[0].name;\r\n                // if (this.sendProfile.imageOne) {\r\n                //     delete this.sendProfile.imageOne;\r\n                // }\r\n            }\r\n            if (type == \"ladiesDress\") {\r\n                await this.$refs.ladiesRef.validate(e);\r\n                this.imageTwo = e.target.files[0];\r\n                this.imageTwoMessage = e.target.files[0].name;\r\n                // if (this.sendProfile.imageOne) {\r\n                //     delete this.sendProfile.imageOne;\r\n                // }\r\n            }\r\n            if (type == \"gentsDress\") {\r\n                await this.$refs.gentsRef.validate(e);\r\n                this.imageThree = e.target.files[0];\r\n                this.imageThreeMessage = e.target.files[0].name;\r\n                // if (this.sendProfile.imageOne) {\r\n                //     delete this.sendProfile.imageOne;\r\n                // }\r\n            }\r\n        },\r\n        sendCeremony(validate, e, type) {\r\n            this.loader = true;\r\n            if (validate == true) {\r\n                this.loader = false;\r\n                return;\r\n            }\r\n\r\n            let _this = 